<template>

  <div>

    <el-row
      type="flex"
      class="row-bg"
      justify="space-around"
    >
      <el-col
        :span="12"
        class="boderDes"
      >
        <div>
          <h4 style="margin-left: 20px;">操作日志查询</h4>
          <div style="margin-right: 20px; margin-left: 20px;">
            <el-form
              :inline="true"
              label-position="left"
              label-width="70px"
              :model="operatForm"
            >

              <el-form-item label="操作者">
                <el-select
                  clearable
                  v-model="operatForm.user"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in userList"
                    :key="item.id"
                    :label="item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="车站">
                <el-select
                  clearable
                  v-model="operatForm.station"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in stationList"
                    :key="item.id"
                    :label="item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="设备">
                <el-select
                  clearable
                  v-model="operatForm.dev"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in devList"
                    :key="item.id"
                    :label=" item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="时间">
                <el-date-picker
                  v-model="operatForm.timeArr"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  value-format="yyyy-MM-dd HH:mm:ss"
                >
                </el-date-picker>
              </el-form-item>
            </el-form>
            <div style="margin-bottom:20px">
              <el-button
                type="primary"
                icon="el-icon-search"
                @click="search(false)"
              >查询</el-button>

              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="deleteData(false)"
                :disabled="delBtnDisable.opera"
              >删除</el-button>
              <el-button
                type="warning"
                icon="el-icon-s-promotion"
                @click="exportData(false)"
              >导出</el-button>

              <el-button
                type="success"
                icon="el-icon-printer"
                @click="printAlarm(false)"
              >打印</el-button>

            </div>
            <edit-table
              ref="operaTable"
              :addSerialNum="true"
              :table_ui_data="uiList"
              :table_data="operatTableData"
              :table_disable_edit="false"
              table_id="operaTable"
              @rowClick="rowOperaClick"
              :pageSize=6
            ></edit-table>
          </div>

        </div>

      </el-col>
      <el-col
        :span="11"
        style="margin-left:60px"
      >
        <div class="boderDes">
          <h4 style="margin-left: 20px;">告警日志查询</h4>

          <div style="margin-right: 20px; margin-left: 20px;margin-bottom: 20px;">
            <el-form
              :inline="true"
              label-position="left"
              label-width="70px"
              :model="alarmForm"
            >

              <el-form-item label="处理人">
                <el-select
                  clearable
                  v-model="alarmForm.user"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in userList"
                    :key="item.id"
                    :label="item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="车站">
                <el-select
                  v-model="alarmForm.station"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in stationList"
                    :key="item.id"
                    :label="item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="设备">
                <el-select
                  v-model="alarmForm.dev"
                  placeholder="请选择"
                >
                  <el-option
                    v-for="item in devList"
                    :key="item.id"
                    :label="item.id + ' ' +item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="时间">
                <el-date-picker
                  v-model="alarmForm.timeArr"
                  type="datetimerange"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  value-format="yyyy-MM-dd HH:mm:ss"
                >
                </el-date-picker>
              </el-form-item>
            </el-form>
            <div style="margin-bottom:20px">
              <el-button
                type="primary"
                icon="el-icon-search"
                @click="search(true)"
              >查询</el-button>

              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="deleteData(true)"
                :disabled="delBtnDisable.alarm"
              >删除</el-button>
              <el-button
                type="warning"
                icon="el-icon-s-promotion"
                @click="exportData(true)"
              >导出</el-button>

              <el-button
                type="success"
                icon="el-icon-printer"
                @click="printAlarm(true)"
              >打印</el-button>

            </div>
            <edit-table
              ref="alarmTable"
              :addSerialNum="true"
              :table_ui_data="alarmUiList"
              :table_data="alarmTableData"
              :table_disable_edit="false"
              table_id="alarmTable"
              @rowClick="rowAlarmClick"
              @dbRowClick="dbRowAlarmClick"
              :tableAttri="tableAttri"
              :pageSize=6
            ></edit-table>
          </div>

        </div>

      </el-col>

    </el-row>

    <el-dialog
      title="告警日志"
      :visible.sync="dialogInfo.visible"
      width="30%"
    >

      <div class="boderDes_q">
        <h3 style="margin-left:20px">告警描述</h3>
        <div style="margin-left:10px;margin-right: 10px;">
          <el-form
            :model="dialogInfo.alarmForm"
            label-width="80px"
          >
            <el-row>

              <el-form-item
                label="设备名"
                prop="dev"
              >
                <el-co1 :span="8">
                  <el-input
                    disabled
                    readonly
                    v-model="dialogInfo.alarmForm.dev"
                    placeholder=""
                  ></el-input>
                </el-co1>

              </el-form-item>

            </el-row>

            <el-row>
              <el-col :span="12">
                <el-form-item
                  label="车站名"
                  prop="staName"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.staName"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item
                  label="告警时刻"
                  prop="time"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.time"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item
                  label="线路名称"
                  prop="lineName"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.lineName"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item
                  label="告警内容"
                  prop="content"
                >
                  <el-input
                    type="textarea"
                    readonly
                    v-model="dialogInfo.alarmForm.content"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-form>
        </div>
      </div>

      <div class="boderDes_q">
        <h3 style="margin-left:20px">处理描述</h3>
        <div style="margin-left:10px;margin-right: 10px;">
          <el-form
            :model="dialogInfo.alarmForm"
            label-width="80px"
          >

            <el-row>
              <el-col :span="12">
                <el-form-item
                  label="处理标记"
                  prop="tag"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.tag"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item
                  label="处理时间"
                  prop="proTime"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.proTime"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item
                  label="处理人"
                  prop="autor"
                >
                  <el-input
                    readonly
                    disabled
                    v-model="dialogInfo.alarmForm.autor"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

            <el-row>
              <el-col>
                <el-form-item
                  label="处理办法"
                  prop="bf"
                >
                  <el-input
                    type="textarea"
                    :readonly="dialogInfo.alarmForm.tag == '1'"
                    v-model="dialogInfo.alarmForm.bf"
                    placeholder=""
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>

          </el-form>
        </div>
      </div>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          :disabled="dialogInfo.alarmForm.tag == '1'"
          @click="dialogHandBtn"
        >处 理</el-button>
        <el-button @click="dialogInfo.visible = false">退 出</el-button>

      </span>
    </el-dialog>

  </div>

</template>

<script>
import { Message } from "element-ui";

export default {
  data() {
    return {
      uiList: [
        {
          label: "操作者",
          prop: "autor",
          disabled: true,
        },
        {
          label: "操作类型",
          prop: "type",
          disabled: true,
        },
        {
          label: "操作内容",
          prop: "content",
          disabled: true,
        },
        {
          label: "操作时间",
          prop: "time",
          disabled: true,
          sortable: true,
        },
        {
          label: "设备名",
          prop: "devName",
          disabled: true,
        },
        {
          label: "车站名",
          prop: "staName",
          disabled: true,
        },
        {
          label: "线路名",
          prop: "lineName",
          disabled: true,
        },
      ],
      alarmUiList: [
        {
          label: "告警设备",
          prop: "dev",
          disabled: true,
        },
        {
          label: "告警内容",
          prop: "content",
          disabled: true,
        },
        {
          label: "告警时间",
          prop: "time",
          disabled: true,
          sortable: true,
          width: 120,
        },
        {
          label: "标记",
          prop: "tag",
          disabled: true,
          formatter: (row, col, cellValue) => {
            var ret = ""; //你想在页面展示的值
            if (cellValue == "1") {
              ret = "已处理"; //根据自己的需求设定
            } else {
              ret = "未处理";
            }
            return ret;
          },
        },
        {
          label: "处理人",
          prop: "autor",
          disabled: true,
        },
        {
          label: "处理办法",
          prop: "bf",
          disabled: true,
        },
        {
          label: "车站名",
          prop: "staName",
          disabled: true,
        },
        {
          label: "线路名称",
          prop: "lineName",
          disabled: true,
        },
      ],
      tableAttri: {
        cellStyle: ({ row }) => {
          // 状态列字体颜色

          if (row.tag == "1") {
            return "color: #0CB618";
          } else {
            return "color: #EA1B29";
          }
        },
      },
      stationList: [],
      devList: [],
      userList: [],
      operatForm: {
        user: "",
        station: "",
        dev: "",
        timeArr: [],
      },
      alarmForm: {
        user: "",
        station: "",
        dev: "",
        timeArr: [],
      },
      operatTableData: [],
      alarmTableData: [],

      delBtnDisable: {
        opera: true,
        alarm: true,
      },
      rowCurAlarm: {},
      rowCurOpera: {},
      dialogInfo: {
        visible: false,
        alarmForm: {},
      },
    };
  },
  mounted() {
    var that = this;
    this.$httpApi.user_list().then((res) => {
      if (res.data.code == "0") {
        that.userList = res.data.dataList;
      }
    });

    this.$httpApi.station_list().then((res) => {
      if (res.data.code == "0") {
        that.stationList = res.data.dataList;
      }
    });

    this.$httpApi.dev_list().then((res) => {
      if (res.data.code == "0") {
        that.devList = res.data.dataList;
      }
    });
  },
  methods: {
    dialogHandBtn() {
      this.dialogInfo.visible = false;
      var that = this;
      this.$httpApi.alarm_handle(this.dialogInfo.alarmForm).then((res) => {
        if (res.data.code != "0") {
          Message.error(res.data.message);
        } else {
          that.dialogInfo.alarmForm.tag = "1";
        }
      });
    },
    rowAlarmClick(val) {
      this.rowCurAlarm = val;
      this.delBtnDisable.alarm = val == undefined;
    },
    dbRowAlarmClick(row) {
      this.dialogInfo.alarmForm = row;
      this.dialogInfo.visible = true;
    },
    rowOperaClick(val) {
      this.rowCurOpera = val;
      this.delBtnDisable.opera = val == undefined;
    },
    search(isAlarm) {
      var that = this;
      if (!isAlarm) {
        this.$httpApi.search_opera_log(this.operatForm).then((res) => {
          if (res.data.code == "0") {
            that.operatTableData = res.data.dataList;
          } else {
            Message.error(res.data.message);
          }
        });
      } else {
        this.$httpApi.search_alarm_log(this.alarmForm).then((res) => {
          if (res.data.code == "0") {
            that.alarmTableData = res.data.dataList;
          } else {
            Message.error(res.data.message);
          }
        });
      }
    },
    deleteData(isAlarm) {
      if (isAlarm) {
        this.$refs.alarmTable.deleteAction();
      } else {
        this.$refs.operaTable.deleteAction(this.rowCurOpera);
      }
    },
    exportData(isAlarm) {
      var tim = new Date();
      var year = tim.getFullYear();
      var month = tim.getMonth() + 1;
      var strDate = tim.getDate();
      var hour = tim.getHours();
      var minut = tim.getMinutes();
      var second = tim.getSeconds();

      var desDate =
        year +
        "-" +
        month +
        "-" +
        strDate +
        "_" +
        hour +
        "_" +
        minut +
        "_" +
        second +
        ".xlsx";

      if (isAlarm) {
        this.$refs.alarmTable.exportDataToExcel(desDate);
      } else {
        this.$refs.operaTable.exportDataToExcel(desDate);
      }
    },
    printAlarm(isAlarm) {
      if (isAlarm) {
        this.$refs.alarmTable.printExcel();
      } else {
        this.$refs.operaTable.printExcel();
      }
    },
  },
};
</script>

<style scoped>
.boderDes {
  border: 1px solid lightgray;
  height: 100vh;
  margin-bottom: 20px;
}
.boderDes_q {
  border: 1px solid lightgray;
  margin-bottom: 20px;
}
</style>